<template>
    <div class="setting">
        <Header>
          <template v-slot:leftnav>
            <i style="color:white;cursor:pointer" class="fas fa-bars" @click.stop="openLeftNav"></i>
          </template>
          <template v-slot:heading>
            <span style="color:white">Setting</span>
          </template>
          <template v-slot:miniplayer>
            <SmallPlayer></SmallPlayer>
          </template>
        </Header>
        <ul>
          <li>
            <i class="fas fa-tv"></i>
            <span>Display</span>
          </li>
          <li>
            <i class="fas fa-volume-up"></i>
            <span>Audio</span>
          </li>
          <li>
            <i class="fas fa-headset"></i>
            <span>HeadSet</span>
          </li>
        </ul>
        <LeftNavigation v-bind:class="{expand:expandLeftNav}"></LeftNavigation>
        <BottomNavigation></BottomNavigation>
    </div>
</template>
<script>
import BottomNavigation from '@/components/BottomNavigation.vue';
import {mapMutations,mapState} from 'vuex';
import Header from '@/components/Header.vue';
import LeftNavigation from  '@/components/LeftNavigation.vue';
import SmallPlayer from '@/components/SmallPlayer.vue';

export default {
  name:'Setting',
  components:{
    BottomNavigation,
    Header,
    LeftNavigation,
    SmallPlayer
  },
  methods:{
   ...mapMutations([
     'openLeftNav'
   ])
  },
  computed:{
    ...mapState([
      'expandLeftNav'
    ]),

  }
}
    
</script>
<style lang="scss">
  ul{
    padding:15px;
    li{
      display:flex;
      cursor:pointer;
      align-items: center;
      padding:12px 0px ;
      i{
        color:orange;
        margin-right:10px;
        font-size:15px
      }
      span{
        color:white;
        font-size: 15px;
      }
    }
  }
</style>