<template>
    <div class="recent">
        <div class="title">
            <span>Recently Played</span>
            <span class="view">View All</span>
        </div>
        <span v-if="!recent.length" class="empty">
            No recent songs
        </span>
        <div class="recent-song" v-for="song in recent" :key="song.name" @click="playit(song)">
            <div>
                <i :class="[currentMusic==song?'playing':'','far fa-play-circle play']"></i>
                <div class="info">
                    <span :class="[currentMusic==song?'playing':'','name']">{{song.name}}</span>
                    <span class="artist">{{song.artist}}</span>
                </div>
            </div>
            <div class="rating">
                <i v-for="rate in song.rating" class="fas fa-star" :key="rate"></i>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState,mapMutations } from 'vuex'
export default {
    computed:{
        ...mapState([
            'recent',
            'currentMusic',
            'song'
        ])
    },
    methods:{
        ...mapMutations([
            'updateSongList',
            'updateCurrentSong',
            'playandpause',
            
        ]),
        playit:function(song){      
            this.updateCurrentSong(song);
            this.updateSongList(this.song);
        }
    }
}
</script>
<style lang="scss">
    .recent{
        padding:0px 15px 25px 15px;
        .title{
            display: flex;
            justify-content: space-between;
            align-items:center;
            span{
                color:white;
                font-size:12px;
            }
            span.view{
                color:orange;
                font-size:10px;
                cursor:pointer
            }
        }
        span.empty{
            text-align:center;
            display:block;
            width:100%;
            color:white;
            opacity:0.5;
            margin-top:30px;
            font-size:12px;
        }
        .recent-song{
            cursor:pointer;
            display:flex;
            justify-content: space-between;
            padding:10px 0px;
            border-bottom:0.3px solid rgba($color: white, $alpha: 0.1);
            div{
                display:flex;
            }
            div i.play{
                    color:orange;
                    font-size:30px;
                    padding-right:10px;
                }
            div i.play.playing{
                animation:rotatePlay 1s infinite linear;
                transform-origin:center;
                animation-direction: alternate;
            }
            div .info{
                display:flex;
                flex-direction: column;
                align-items:flex-start;
                .name{
                    color:white;
                    font-size:12px;
                    opacity:0.7
                }
                .name.playing{
                    opacity:1;
                }
                .artist{
                    color:white;
                    font-size:10px;
                    opacity:0.5;
                }
            }
            .rating{
                color:orange;
                font-size:12px;
                opacity:0.7;
                i{
                    padding-left:2px;
                }
            }
        }
    }
    @keyframes rotatePlay {
        100%{
            transform:scale(0.5,0.5)
        }
    }
</style>