<template>
    <div class="song" @click="playit(s)">
        <div class="song-info">
            <img :src="require('../musics/images/'+s.image)" alt="">

            <div>
                <span :class="[currentMusic==s?'playing':'','name']">{{s.name}}</span>
                <span class="artist">{{s.artist}}</span>
            </div>
        </div>
        <i class='far fa-play-circle play'></i>
    </div>
</template>
<script>
import { mapState,mapMutations } from 'vuex'
export default {
    props:[
        's'
    ],
    computed:{
        ...mapState([
            'currentMusic',
            'song'
        ])
    },
    methods:{
        ...mapMutations([
            'updateSongList',
            'updateCurrentSong',
            'playandpause',
            
        ]),
        playit:function(song){      
            this.updateCurrentSong(song);
            this.updateSongList(this.song);
        }
    }
}
</script>
<style lang="scss" scoped>
    .song{
        display:flex;
        justify-content: space-between;
        align-items: center;
        padding:10px 0px 10px 0px;
        cursor:pointer;
        border-bottom:0.3px solid rgba($color: white, $alpha: 0.1);
        .song-info{
            display:flex;
            align-items: center;
            img{
                width:40px;
                height:40px;
                border-radius:50%;
                margin-right:10px;
            }
            div{
                display:flex;
                flex-direction: column;
                align-items: flex-start;
            }
            div .name{
                    color:white;
                    font-size:12px;
                    opacity:0.7
                }
            div .name.playing{
                opacity:1;
                color:orange;
            }
            div .artist{
                color:white;
                font-size:10px;
                opacity:0.5;
            }
            
        }
        i{
                font-size:25px;
                color:orange;
        }
        
    }
</style>