<template>
    <div class="main-player">
        <Header>
            <template v-slot:playerHeader>
                <i @click="this.$router.go(-1)" class="back fas fa-chevron-left"></i>
                <marquee class="title">{{currentMusic.name+'-'+currentMusic.artist}}</marquee>
                <i class="more fas fa-ellipsis-v"></i>
            </template>
        </Header>
        <transition name="fade">
            <div :class="['playing','spinimg']">
                <img :src="require('../musics/images/'+currentMusic.image)" alt="">
            </div>
        </transition>
        <AnimateBar></AnimateBar>
        <PlayerControl></PlayerControl>
    </div>
</template>
<script>
import { mapState } from 'vuex';
import Header from '@/components/Header.vue'
import AnimateBar from '@/components/AnimateBar'
import PlayerControl from '@/components/PlayerControl'

export default {
    name:'MainPlayer',
    computed:{
        ...mapState([
            'currentMusic',
            'isPlaying',
            'animation'
        ])
    },
    components:{
        Header,
        AnimateBar,
        PlayerControl
    },
    watch:{
        //stop playing music animation
        animation:function(val){
            if(val){
                document.querySelector('.spinimg').style.animationPlayState="running"
            }else{
                document.querySelector('.spinimg').style.animationPlayState="paused"
            }
        }
    }
}
</script>
<style lang="scss">
    div.spinimg{
        width:200px;
        height:200px;
        border-radius: 50%;
        padding:10px;
        border:1px dashed rgba(255,255,255,0.6);
        background:transparent;
        margin:0 auto;
        margin-top:30px;
        img{
            border-radius: 50%;
            width:100%;
            height: 100%;
        }
    }
    div.spinimg.playing{
        animation:rotate 10s infinite linear;
        animation-fill-mode: forwards;
    }
    @keyframes rotate{
        0%{
            transform:rotateZ(0)
        }
        100%{
            transform:rotateZ(720deg);
        }
    }
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
        }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }
</style>