<template>
    <div class="hot">
        <h3>Hot Recommended</h3>
        <div class="hot-songs">
            <div class="hot-song" v-for="song in getHotSong" v-bind:key="song.name" @click="playit(song)">
                <img v-bind:src="require('../musics/images/'+song.image)" alt="">
                <span>{{song.name}}</span>
                <span>{{song.artist}}</span>
            </div>
        </div>
    </div>
</template>
<script>
import {mapGetters, mapMutations, mapState} from 'vuex';

export default {
    computed:{
        ...mapGetters([
            'getHotSong',
            
            
        ]),
        ...mapState([
            'songslist',
            'currentMusic'
        ])
    },
    methods:{
        ...mapMutations([
            'updateSongList',
            'updateCurrentSong',
            'playandpause'
        ]),
        playit:function(song){      
            this.updateCurrentSong(song);
            this.updateSongList(this.getHotSong);
            this.$router.push({name:'Play',query:{'name':song.name}})
        },

    }
}
</script>
<style lang="scss">
.hot{
    width:100vw;
    height:auto;
    h3{
        color:white;
        padding:5px 0px 10px 15px;
        font-size: 10px;
    }
    .hot-songs{
        padding:0 15px;
        width:100%;
        overflow-x: auto;
        &::-webkit-scrollbar{
            width: 0;
        }
        display: flex;
        align-items: center;
        .hot-song{
            flex-basis: 200px;
            flex-grow: 0;
            flex-shrink: 0;
            padding-right:20px;
            border-radius:10px;
            display:flex;
            align-items: flex-start;
            flex-direction: column;
            color:white;
            img{
                width:100%;
                height: 100px;
                border-radius:10px;
            }
            span:nth-of-type(1){
                font-size:13px;
                margin-top:5px;
            }
            span:nth-of-type(2){
                font-size: 10px;
                opacity:0.5;
            }
        }
    }
}
</style>