<template>
    <div class="player">
        <audio id="player" ref="player" preload="metadata">
            <source :src="!currentMusic.file==''?require('../musics/'+currentMusic.file):''" type="audio/mpeg">
        </audio>
    </div>
</template>
<script>
import { mapMutations, mapState } from 'vuex'

export default {
    data:function(){
        return {
            currentSongObject:document.querySelector('#player')
        }
    },
    computed:{
        ...mapState([
            'currentMusic',
            'isPlaying',
            'songslist', 
            'repeat',
            'currentTime',
            'tempTime',
            'currentMusicLength'
        ])
    },
    methods:{
        ...mapMutations([
            'updateTempTime',
            'updateTime',
            'updateMusicLength',
            'nextSong',
            'updateRecent'
        ])
    },
    watch:{
        isPlaying:function(val){
            if(val){
                this.$refs.player.play();
            }else{
                this.$refs.player.pause();                
            }
        },
        currentMusic:function(song){
            var updateTime=this.updateTime;
            console.log(song);
            console.log(this.$refs.player);
            console.log(this.isPlaying);
            this.$refs.player.load();
            this.$refs.player.play();

            this.updateRecent(song);
            var player=this.$refs.player;
            var update=setInterval(function(){               
                updateTime(player.currentTime);
            },1000,player,updateTime);

            var updateMusicLength=this.updateMusicLength;
            player.updateMusicLength=updateMusicLength;
            player.onloadedmetadata=function(e){
                this.updateMusicLength(this.duration);
            }
            
        },
        repeat:function(val){
            this.$refs.player.currentTime=0;
            //repeat current song
        },
        tempTime:function(val){
            this.$refs.player.currentTime=val;
        }
    }
}
</script>
<style lang="scss">
    .player{
        visibility: hidden;
    }
</style>