<template>
    <div class="search">
        <Header>
        <template v-slot:searchfield>
            <div class="searchfield">
                <i class="fas fa-search"></i>
                <input v-model="searchvalue" autofocus type="text" id="search" placeholder="Search you songs">
            </div>
        </template>
        <template v-slot:cancel>
            <i class="fas fa-times" @click="$router.go(-1)" style="color:white;cursor:pointer"></i>
        </template>
        </Header>
        <div class="result">
            <span v-if="result.length==0" class="error">No result</span>
            <span @click="playit(r)" v-for="r in result" :key="r.name">{{r.name}} - {{r.artist}}</span>
        </div>
    </div>
</template>
<script>
import Header from '@/components/Header.vue'
import { mapState,mapMutations } from 'vuex';

export default {
    data:function(){
        return {
            searchvalue:'',
            result:[]
        }
    },
    mounted:function(){
        document.getElementById('search').focus();
    },
    components:{
        Header
    },
    computed:{
        ...mapState([
            'song'
        ])
    },
    watch:{
        searchvalue:function(val){
            this.result=this.song.filter((e)=>{
                return !(e.name.search(val)==-1)||!(e.artist.search(val)==-1);
            })
            console.log(this.result)
        }
    },
    methods:{
        ...mapMutations([
            'updateSongList',
            'updateCurrentSong',
            'playandpause'
        ]),
        playit:function(song){      
            this.updateCurrentSong(song);
            this.updateSongList(this.song);
            this.$router.push({name:'Play',query:{'name':song.name}})
        },
    }
}
</script>
<style lang="scss">

div.searchfield{
    display:flex;
    justify-content: start;
    align-items: center;
    background:#292E4B;
    border-radius:25px;
    width:80%;
    height:28px;
    padding:0 10px;
    #search{
        margin-left:10px;
        width:100%;
        border:none;
        outline:none;
        background: transparent;
        color:white;
        &::placeholder{
            font-size: 12px;
            letter-spacing: 1px;
        }
    }
    i{
        color:grey;
    }
}
.result{
    padding:20px 15px 20px 15px;
    display:flex;
    flex-direction: column;
    span.error{
        color:white;
        opacity:0.5;
        font-size:10px;
        width:100%;
        display:block;
        text-align: center;
    }
    span{
        color:white;
        padding:10px 0px;
        cursor:pointer;
        &:hover{
            color:orange;
        }
    }
}
</style>