<template>
    <div class="player-control">
        <div class="progress" @click="changeCurrentTime">
            <div class="progress-bar"></div>
            <div class="time">
                <span class="left">
                    {{convertToMin(currentTime)}}
                </span>
                <span class="right">
                    {{convertToMin(currentMusicLength)}}
                </span>
            </div>
        </div>
        <div class="controls">
            <div class="mode" @click="toggleMode">
                <i v-if="playMode=='shuffle'" class="fas fa-random"></i>
                <i v-if="playMode=='loop'" class="fas fa-redo"></i>
                <i v-if="playMode=='repeat'" class="fas fa-route"></i>
            </div>
            <div class="prev" @click="prevSong">
                <i class="fas fa-step-backward"></i>
            </div>
            <div class="play" @click="playandpause">
                <i v-if="!isPlaying" class="fas fa-play"></i>
                <i v-else class="fas fa-pause"></i>
            </div>
            <div class="next" @click="nextSong">
                <i class="fas fa-step-forward"></i>
            </div>
            <div class="like" v-bind:class="[favourite.indexOf(currentMusic)==-1?'':'favourite']">
                <i class="fas fa-heart" @click="addToFav(currentMusic)"></i>
            </div>
        </div>
    </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
export default {
    methods:{
        ...mapMutations([
            'playandpause',
            'nextSong',
            'prevSong',
            'toggleMode',
            'updateTempTime',
            'addToFav',
            'nextSong'
        ]),
        changeCurrentTime:function(e){
            var width=e.offsetX;
            var originalwidth=document.querySelector('.progress').getBoundingClientRect().width;
            var percent=(width/originalwidth)*100;
            var length=(percent/100)*this.currentMusicLength;
            this.updateTempTime(length);
        },
        convertToMin:function(val){
            val=Math.round(val);
            var min=Math.round(val/60);
            var second=Math.round(val%60);
            // if(second<10){
            //     second='0'+second
            // }
            return min+":"+second;
        }
        
    },
    computed:{
        ...mapState([
            'isPlaying',
            'playMode',
            'currentTime',
            'currentMusicLength',
            'currentMusic',
            'favourite'
        ]),
        
    },
    watch:{
        currentTime:function(length){
            var percent=(length/this.currentMusicLength)*100;
            document.querySelector('.progress-bar').style.width=percent+"%";
            if(length==this.currentMusicLength){
                this.nextSong();
            }
        }

    },
    filters:{
        
    }
}
</script>

<style lang="scss">
    .player-control{
        position: absolute;
        bottom:0;
        background:transparent;
        display:flex;
        flex-direction:column;
        align-items:center;
        justify-content: center;
        width:100%;
        padding:30px 0;
        .controls{
            display:flex;
            justify-content: space-around;
            width:100%;
            color:white;
            .mode,.like{
                opacity:0.5;
                cursor:pointer;
            }
            .play i{
                font-size:20px;
                cursor:pointer;
            }
            .prev,.next{
                cursor:pointer
            }
            .like.favourite{
                opacity:1;
            }
            .like.favourite i{
                color:red;
                opacity:1;
            }
        }
        .progress{
            width:85%;
            height:5px;
            border-radius:25px;
            background: white;
            margin-bottom:50px;
            cursor:pointer;
            .progress-bar{
                width:0%;
                height: 100%;
                background:orange;
                border-radius:25px;
            }
            .time{
                display:flex;
                margin-top:5px;
                justify-content:space-between;
                align-items:center;
                color:rgba($color: white, $alpha: 0.5);
                font-size:10px
            }
        }
        

    }
</style>