<template>
    <div :class="['playing','animatebar']">
        <span class="bar one"></span>
        <span class="bar two"></span>
        <span class="bar three"></span>
        <span class="bar four"></span>
        <span class="bar five"></span>
        <span class="bar six"></span>
        <span class="bar seven"></span>
        <span class="bar eight"></span>
        <span class="bar nine"></span>
        <span class="bar ten"></span>
        <span class="bar ele"></span>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
    computed:{
        ...mapState([
            'isPlaying',
            'animation',
        ])
    },
    watch:{
        //stop playing music animation
        animation:function(val){
            if(val){
                document.querySelectorAll('.bar').forEach((e)=>{
                e.style.animationPlayState="running"
            })
            }else{
                document.querySelectorAll('.bar').forEach((e)=>{
                e.style.animationPlayState="paused"
            })
            }
        }
    }
}
</script>

<style lang="scss" scoped>
    div.animatebar{
        width:100%;
        height: 50px;
        margin:30px auto;
        display:flex;
        align-items:center;
        justify-content: center;
        .bar{
            // height:50px;
            width:10px;
            background:white;
            display:block;
            margin:0 5px;
            border-radius:25px;
        }
        .one,.ele{
            height:10px;
            opacity:0.5
        }
        .two,.ten{
            height:20px;
        }
        .three,.nine{
            height:25px;
        }
        .four,.eight{
            height:30px;
        }
        .five,.seven{
            height:40px;
        }
        .six{
            height:50px;
            opacity:0.5
        }
    }
    .playing{
        .one,.ele{
            animation:play 0.7s infinite alternate forwards;
        }
        .two,.ten{
            animation:play 0.4s infinite alternate forwards;
        }
        .three,.nine{
            animation:play 0.5s infinite alternate forwards;
        }
        .four,.eight{
            animation:play 0.6s infinite alternate forwards;
        }
        .five,.seven{
            animation:play 0.7s infinite alternate forwards; 
        }
        .six{
            animation:play 0.4s infinite alternate forwards;
        }
    }
    @keyframes play{
        50%{
            height: 50px;
        }
        100%{
            height:10px;
        }
    }
</style>