<template>
    <div class="left-navigation" @click.stop="openLeftNav">
        <div class="user-info">
            <img src="../assets/avator.svg" alt="">
            <h6>{{userinfo.name}}</h6>
            <div class="song-info">
                <div class="song">
                    <span>{{song.length}}</span>
                    <span>Songs</span>
                </div>
                <div class="album">
                    <span>{{album.length}}</span>
                    <span>Albums</span>
                </div>
                <div class="artist">
                    <span>{{artist.length}}</span>
                    <span>Artists</span>
                </div>
            </div>
        </div>
        <div class="quick-actions">
            <div @click="this.$router.push(action.path)" class="quick-action" v-for="action in quickactions" :key="action.title">
                <span v-html="action.icon"></span>
                <span>{{action.title}}</span>
            </div>
        </div>
        <span class="close" @click.stop="closeLeftNav">
            <i class="fas fa-times"></i>
        </span>
    </div>
</template>
<script>
import { mapState,mapGetters,mapMutations } from 'vuex';

export default {
    methods:{
        ...mapMutations([
            'closeLeftNav',
            'openLeftNav'
        ]),
    },
    computed:{
        ...mapState([
            'userinfo',
            'song',
            'album',
            'artist',
            'quickactions'
        ])
    }
}
</script>
<style lang="scss">
.left-navigation{
    height: 100vh;
    width:85%;
    background:#10121D;
    position:fixed;
    top:0;
    z-index: 10;
    left:0;
    transform:translateX(-100%);
    transition:0.3s all;
    .user-info{
        width:100%;
        height:30%;
        background:rgba(255,255,255,0.03);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        h6{
            padding-top:10px;
            color:white;
            letter-spacing: 1px;
        }
        .song-info{
            width:100%;
            display:flex;
            justify-content: space-around;
            align-items: center;
            padding-top:20px;
            div{
                display: flex;
                flex-direction: column;
                align-items: center;
                span{
                    color:white;
                    font-size:12px;
                }
            }
        }
    }
    .quick-actions{
        padding:30px 20px;
        .quick-action{
            display: flex;
            align-items:center;
            justify-content: start;
            border-bottom:0.1px solid rgba(255,255,255,0.05);
            span{
                margin:15px 20px 15px 0px;
                color:white;
                font-size: 12px;
            }
            span:nth-child(1){
                color:orange;
            }
            &:hover{
                background:rgba(255,255,255,0.03);
            }
        }
    }
    span.close{
        position: fixed;
        z-index: 20;
        right:20px;
        top:20px;
        color:white;
        cursor: pointer;
    }
}
.left-navigation.expand{
    transform:translateX(0);
    box-shadow:3px 0px 6px rgba(0,0,0,0.3)
}
</style>