<template>
    <div class="miniplayer" :class="[isPlaying?'playing':'']">
        <img src="@/assets/miniplayer.png" alt="" @click="goToPlayer">
    </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
    name:'SmallPlayer',
    computed:{
        ...mapState([
            'isPlaying',
            'currentMusic'
        ])
    },
    methods:{
        goToPlayer:function(){
            if(this.isPlaying){
                this.$router.push({name:'Play',query:{'name':this.currentMusic.name}})
            }
        }
    }
}
</script>
<style lang="scss">
.miniplayer{
    display:flex;
    align-items: center;
    justify-content: center;
    img{
        height:30px;
        cursor:pointer;
    }
    animation:rotatePlayer 10s infinite linear;
    animation-play-state: paused;
}
.miniplayer.playing{
    animation-play-state: running;
}
@keyframes rotatePlayer {
    100%{
        transform: rotateZ(720deg);
    }
}
</style>